*{
    font-family: 'Poppins', sans-serif;
     animation: transitionIn 0.9s;
  }
  
  @keyframes transitionIn{
    from{
      opacity: 0;
      transform: translate(-10px);
    }
    to{
      opacity: 1;
      transform: translate(0);
    }
  }
   body{
      background-image: url(https://wallpapercave.com/wp/wp8002958.jpg);
      background-repeat: no-repeat;
      background-size: cover;
    }
  
  #logo{
    position: fixed;
    left:30px;
    color: red;
    text-decoration: none;
    font-weight: 300;
    cursor: default;
    display: inline-block;
    padding: 10px 20px;
    font-weight: bold;
    font-size: 125%;
    top:-10px;
  }
  #logo span{
    color: black;
  }
  .navbar {
    background-color: rgba(138, 41, 41, 0.078);
    display: flex;
    justify-content: flex-end;
    padding: 0.5rem 0;
    backdrop-filter:blur(2px);
    border-bottom: 4px solid rgba(194, 41, 41, 0.2);
  }
  .navbar a {
    display: inline-block;
    padding: 10px 20px;
    text-decoration: none;
    color: black;
    font-weight: bold;
    font-size: 120%;
    left: -100%;
    width: 8%;
  }
  .navbar a:hover {
    color: rgb(156, 42, 42);
  }
  
  .App {
    text-align: center;
    margin: 2rem;
  
  }
  h1 {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 50px;
    padding-top: -20%;
    font-size: 2rem;
    color: #333;
  }
  .container{
    flex-direction: column;
    border-radius: 100px;
    padding: 50px; 
    box-shadow:  0 -3px 15px rgb(10, 175, 230), 
    0 3px 10px rgb(242, 242, 243);
    border: none;
    margin: 0 20%;
  }
  
  label{
    font-weight: bold;
    display: flex;
    justify-content: center;
  
  }
  
  input{
   padding: 10px;
    width: 200px;
    border: none; 
    margin-bottom: 10px;
    border-radius: 100px;
    margin-right: 10px;
    border-top: 30px;
    transition: border 0.3s, box-shadow 0.3s, opacity 0.3s;
    box-shadow: inset  0 -3px 15px rgb(124, 203, 230), 
   inset 0 3px 10px rgb(124, 203, 230);
  }
  
  label {
    font-weight: bold;
    color: #333;
    margin-bottom: 8px;
  }
  
  select{
    padding: 3px;
    border: 2px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
    margin-bottom: 20px;
  }
  table {
    width: 100%;
    border-collapse: collapse;
    
  }
  th, td {
    border: 1px solid #dddddd;
    text-align: center;
    padding: 8px;
  }
  th {
    background-color: #f2f2f2;
  }
  
  button{
    background-color: #0077FF;
    margin-left: 5px;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s ease, transform 0.2s ease;
  }
  button:hover {
    background-color: grey;
    transform: scale(1.05); 
  }